body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,0.4)), to(rgba(0,0,0,0.4))), url("background-for-food-3.jpg");
    background-image: -webkit-linear-gradient(rgba(0,0,0,0.4), rgba(0,0,0,0.4)), url("background-for-food-3.jpg");
    background-image: -o-linear-gradient(rgba(0,0,0,0.4), rgba(0,0,0,0.4)), url("background-for-food-3.jpg");
    background-image: linear-gradient(rgba(0,0,0,0.4), rgba(0,0,0,0.4)), url("background-for-food-3.jpg");
    background-size: cover;
    background-position: center;
    height: 100vh;
    /* background-color: #272E4F; */
    /* background-color: #2C365D; */
    /* background-color: #606470; */
  }



