.modal-wrapper{
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background: rgba(222, 222, 222, 0.1);
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.modal-window {
	padding: 2em;
	width: 50%;
	font-family: Avenir;
	text-align: center;
	background: rgba(255, 255, 255, 0.8);
	color: black;
	border-radius: 8px;
}

.modal-shown{
	height: 100vh;
	overflow: hidden;
}

.btnSuccess {
	padding: 2em;
	text-align: center;
	font-family: Avenir;
	background: rgba(155, 155, 155, 0.1);
	color: black;
	border-radius: 8px;
}



